const apFixed = new APlayer({
  element: document.getElementById('aplayer-fixed'),
  mutex: true,
  theme: '#97dffd',
  order: 'random',
  lrcType: 3,
  fixed: true,
});
$.ajax({
  // 你的歌单id
  url: 'https://api.i-meto.com/meting/api?server=netease&type=playlist&id=3778678',
  success: function (list) {
    apFixed.list.add(list);
  }
});
